@charset"UTF-8";
.fix { position: fixed; right: 0; bottom: 0; font-size: 50px; z-index: 9999; }
section {
	width: 100%; 
	.inner_wrap { max-width: 1600px; margin: 0 auto; }	
   h1 { color: #2E2E2E; font-size: em(40); }
   @include mobile() {
      h1 { font-size: em(54); }
   }
}
.sec1 {
   /* min-width: 1024px; */ padding: em(41) 0 em(42); background-color: #F8F8F8;
	.inner_wrap {
      position: relative; padding:0 em(50);
		& > img { 
         width: 100%; border: em(1) solid #EAEAEA; 
         @include borderRadius(em(14));
         @include boxSizing;
      }
	}
	.txt_wrap {
		width: 38%; position: absolute; top: 15%; left: 10%;
      & > img { width: em(140); }
		h1 { padding: 7% 0 0; font-size: em(50); color:$black; line-height: em(54); font-weight: $semiBold; }
      p { padding: 5% 0 0; font-size: em(28); line-height: em(32);}
	}
   @include mobile() {
      min-width: auto; padding: em(29) 0 em(47);
      .styler_logo { width: em(224); margin: 0 auto; }
      .inner_wrap {
         padding:em(21) em(40) 0;
         & > img { border-color: #D2D2D2; }
      }
      .txt_wrap {
         width: 49%; top: 18%; left: 10%;
         & > img { display: none; }
         h1 { width:90%; padding:0; font-size: em(52); line-height: em(56); }
         p { padding: em(15) 0 0; font-size: em(32); line-height: em(40);}
      }
   }
}
.sec2 {
	.top_nav { 
      width: 100%; height:em(100); text-align: center; margin: 0 auto; padding: em(27) 0; border-top: 1px solid #F0F0F0;
      border-bottom: 1px solid #F0F0F0; background-color: $white; 
      &.on { 
         position: fixed; top: 0; left: 0; z-index: 2; 
         & + .inner_wrap  { margin: em(100) auto 0; }
      }
      @include boxSizing;
      ul { display: block; margin: 0 auto; width: fit-content; @extend %clearfix; }
      li {
         float: left; margin: 0 0 0 em(10);
         &:first-of-type { margin: 0; }
         button {
            display: block; padding: em(13) em(40); border: em(1) solid #D9D9D9; font-size: em(18); 
            color: #848484; font-weight: $semiBold; position: relative;
            @include boxSizing;
            @include borderRadius(em(50)); 
            &.active {
               color: #C80651; border-color: #c80651;
               &:before { border-color: #c80651; }
            }
            &:before {
               width: 100%; height: 100%; content: ""; display: block; position: absolute;
               border: em(1) solid transparent; left: 0; top: 0;
               @include boxSizing;
               @include borderRadius(em(50)); 
            }
            &:hover { 
               color: #C80651; border-color: #c80651;
               &:before { border-color: #c80651; }
            }
         }       
      }
   }
	.inner_wrap {
		max-width: 1600px; margin: 0 auto; padding:0 em(50);
      h1 { padding: em(95) 0 0; text-align: center; }
		ul { width:fit-content; margin:0 auto; padding: em(68) 0 em(92); @extend %clearfix; }
      /* .swiper {
         &.max_item {
            li { 
               margin: 0 0 0 1.56%; 
               &:first-of-type { margin: 0; }
            }
         }
      } */
		li {
			width: 18.75%; float: left; 
         img { width:100%; }
         div { overflow: hidden; @include borderRadius(em(15)); }
         strong { display: block; padding: em(15) 0 0; font-size: em(30); font-weight: $semiBold; color: #313131; }
         p { padding: em(7) 0 0; font-size: em(20); color: #4C4C4E; font-weight: $regular; line-height: em(24); }
         &.no_open {
            div { 
               position: relative; text-align: center;
               &:before {
                  width: 100%; height: 100%; display: block; content: "Comming Soon"; position: absolute; top: 0; left: 0;
                  padding: 32% 0 0; font-weight: $bold; background-color: rgba(0,0,0,.7); color: $white; font-size: em(32);
                  @include boxSizing;
               }
             }
            strong { color:#939393; }
            p { color:#939393; }
         }
		}
	}
   @include mobile() {
      .top_nav { 
         padding: em(20) em(40);
         ul { width: 100%; }
         li {
            margin: 0 0 0 1%; 
            &:nth-of-type(1) { width:33.86%; }
            &:nth-of-type(2) { width:25.14%; }
            &:nth-of-type(3) { width:11.91%; }
            &:nth-of-type(4) { width:26.01%; }
           button { width: 100%; padding: em(6) 0 em(8); font-size: 3.4vw; line-height: em(40); }
         }
      }
      .inner_wrap {
         padding:0 em(40);
         h1 { padding: em(83) 0 0; }
         ul { 
            flex-wrap: wrap; justify-content: space-between; padding: em(67) 0 0; 
            &:after { display: none; }
         }
         li {
            width: 47.5%;
            strong { padding: em(20) 0 0; font-size: em(40); }
            p { padding: em(10) 0 em(50); font-size: em(32); line-height: em(38); }
            &.no_open {
               div { 
                  &:before { padding: 30% 0 0; font-size: em(42); font-weight: $semiBold; }
               }
            }
         }
      }
   }
}
.sec3 {
   .inner_wrap { max-width: 1600px; margin: 0 auto; }
   .about_elm {
      background-color: #F9F9F9; text-align: center;
      h1 { padding:em(95) 0 0; }
      .sb_txt { 
         padding: em(20) 0 0; font-size: em(24); line-height: em(28); font-weight: $regular;
         strong { font-weight: $semiBold; }
      }
      ul { padding: em(50) 0 em(100); display: inline-block; margin: 0 auto; @extend %clearfix; }
      li { 
         width: em(180); height:em(160); float: left;  padding:em(24) 0 em(17); border-left: 1px solid #F0F0F0;
         @include boxSizing;
         &:first-of-type { border-left:0; }
         & > div { height: em(85); display: flex; align-items: center; justify-content: center; }
         p { padding:em(20) 0 0; font-size: em(18); font-weight: $bold; color:#4C4C4E; }
         .bedding {img { width: em(73); }}
         .dress {img { width: em(74); }}
         .cotton_toy {img { width: em(73); }}
         .coat {img { width: em(64); }}
         .leather {img { width: em(63); }}
         .padding {img { width: em(62); }}
      }
   }
   .video_cont {
      padding: em(60) 0; background-color: $white;
      > div { 
         width:71.56%; margin: 0 auto; 
         p { padding:em(15) 0 0; font-size: em(28); font-weight: $semiBold; color:#313131; }
      }
      .video_wrap {
         overflow: hidden;
         @include borderRadius(em(14));
         img { width:100%; }
         
      }
      .sb_video {
         width: 77.81%; height:0; padding:0 0 26.5%; text-align: center; margin: 0 auto; overflow: hidden; position: relative;
         &.more_add { 
            height: auto; padding:0; 
            .more_video_btn {
               button {
                  img { @include transform(rotate(180deg)) }
               }
            }
         }
         ul { padding: em(45) 0 em(80); display: inline-block; @extend %clearfix; }
         li { 
            width: 24.09%; padding:0 0 em(65); float: left; margin: 0 0 0 1.20%; text-align: left;
            &:nth-of-type(4n+1) { margin:0; }
            p { padding: em(15) 0 0; font-size: em(28); color: #939393; font-weight: $semiBold; }
            img { width:100%; }
            div { overflow: hidden; @include borderRadius(em(12)); }
            &.no_open {
               div {
                  position: relative;
                  &:before { 
                     width: 100%; height: 100%; display: block; padding: 26% 0 0; content: "Coming Soon";
                     position: absolute; left: 0; top: 0; background-color: rgba(0,0,0, .8); color: $white; 
                     font-weight: $bold; font-size: em(30); 
                     @include boxSizing;
                  }
               }
            }
         }
      }
      .more_video_btn {
         width: 100%; position: absolute; text-align: center; bottom:0; background-color:$white;
         button { 
            border: em(4) solid #C80651; padding: em(23) em(85) em(23) em(85); display: inline-block; 
            font-size: em(30); font-weight: $semiBold; color: #C80651; position: relative;
            @include boxSizing;
            @include borderRadius(em(100));
            & > span {
               width: em(30); height: em(30); display: inline-block; margin:0 0 0 em(10);
               img { width: 100%; }
            }
         }
      }
   }
   @include tablet() {
      .about_elm {
         ul {
             width:em(550); padding: em(50) 0 em(100); display: flex; flex-wrap: wrap; justify-content: space-between;  
             &:after { display: none; }
         }
         li { 
            border-left: 0; float: initial; box-sizing: content-box;
            &:nth-of-type(n+4) { padding: 5% 0 0; }
         }
      }
      .video_cont {
         .sb_video {
            width: 77.81%; padding:0 0 33%;
            li { 
               width: 31.87%; padding:0 0 em(40); margin: 0 0 0 2.18%;
               &:nth-of-type(4n+1) { margin:0 0 0 2.18%; }
               &:nth-of-type(3n+1) { margin:0; }
            }
         }
      }
   }
   @include mobile() {
      .about_elm {
         h1 { padding:em(83) 0 0; }
         .sb_txt { 
            width:68%; margin:0 auto; padding:em(48) 0 0;
            font-size: em(40); line-height: em(48); 
         }
         ul { width: 72.39%; }
         li { 
            & > div { height: em(100); }
            p { font-size: em(30); padding:em(13) 0 0; }
            .bedding {img { width: em(83); }}
            .dress {img { width: em(84); }}
            .cotton_toy {img { width: em(83); }}
            .coat {img { width: em(74); }}
            .leather {img { width: em(73); }}
            .padding {img { width: em(72); }}
         }
      }
      .video_cont {
         padding:em(80) em(40); 
         & > div {
            width: 100%;
            p { font-size: em(40);}
         }
         .sb_video {
            width: 100%; padding: 0 0 66.5%;
            ul { width: 100%; padding: em(40) 0 em(150); }
            li { 
               width: 47.96%; padding:0 0 em(45); margin: 0 0 0 3.92%;
               &:nth-of-type(4n+1) { margin:0 0 0 3.92%; }
               &:nth-of-type(3n+1) { margin:0 0 0 3.92%; }
               &:nth-of-type(2n+1) { margin:0; }
               p { padding: em(15) 0 0; font-size: em(40); text-align: left;}
               button { width:100%; display: block; }
               &.no_open {
                  div {
                     &:before { 
                        padding: 26% 0 0; 
                        font-weight: $semiBold; font-size: em(42); 
                     }
                  }
               }
            }
         }
         .more_video_btn {
            button { 
               width: 100%; padding: em(33) em(154) em(31) em(153); font-size: em(40);
               & > span { width: em(40); height: em(40); margin: 0 0 0 em(10); }
            }
         }
      }
   }
}
.sec4 {
   background-color: #EDEDED;
   .inner_wrap {
      text-align: center; padding:0 em(50) em(120);
      h1 { padding:em(80) 0 0; }
      > div { padding:em(66) 0 0; }
      dl {
         overflow: hidden; border: em(2) solid #E6E6E6; background-color: $white; text-align: left; margin: em(10) 0 0;
         &:first-of-type { margin:0; }
         @include boxSizing;
         @include borderRadius(em(14));
         &.on {
            dt {
               &:after { @include transform(rotate(0)); }
            }
         }
      }
      dt {
         padding: 0 0 0 em(133); position: relative;
         &:after {
            width: em(24); height: em(15); position: absolute; right: em(60); top: 45%; content: ""; display: block;
            background: url(./../../images/main/question_icon.png) 50% 50% no-repeat; background-size: cover;
            @include transition(all, .25s);
            @include transform(rotate(-180deg));
         }
         &:before { position: absolute; display: block; content: "Q"; left: em(60); top: em(30); font-size: em(40); font-weight: $bold; }
         button { width: 100%; text-align: left; padding: em(32) em(300) em(32) 0; display: block; }
         p { font-size: em(34); font-weight: $regular; line-height: em(42); }
      }
      dd {
         height: 0; padding: 0 0 0 em(133); position: relative;
         &:before { position: absolute; display: block; content: "A"; left: em(60); color:#C80651; top: em(40); font-size: em(40); font-weight: $bold; }
         &:after { content: ""; position: absolute; display: block; width: 100%; top: 0; left: 0; height: 1px; background-color: #E6E6E6; }
         p { 
            padding:em(43) em(300) em(43) 0;
            font-size: em(28); font-weight: $regular; color: #939393; line-height: em(35); 
         }
      }
   }
   @include mobile() {
      .inner_wrap {
         padding:0 em(40) em(88);
         h1 { padding:em(82) 0 0; }
         > div { padding:em(60) 0 0; }
         dl { margin: em(30) 0 0; }
         dt {
            padding: 0 0 0 em(102);
            &:after {
               width: em(33); height: em(20); right: em(40);
               background: url(./../../images/main/question_icon_m.png) 50% 50% no-repeat; 
               background-size: cover;
            }
            &:before { left: em(35); top: em(53); font-size: em(54); }
            button { padding: em(60) em(100) em(60) 0; }
            p { font-size: em(44); line-height: em(50); }
         }
         dd {
            padding:0 0 0 em(102); 
            &:before { left: em(35); top: em(58); font-size: em(54); }
            p { padding: em(60) em(75) em(60) 0; width: auto; font-size: em(44); line-height: em(55); }
         }
      }
   }
}
.sec5 {
   background-color: #F6F6F6;
   .inner_wrap {
      padding:em(94) 0 em(105) em(50); text-align: center; 
      .swiper { padding: em(66) 0; }
      .swiper-slide {
         padding: em(25) em(25) em(35); background-color: $white; text-align: left;
         @include borderRadius(em(14));
         @include boxSizing;
         .item_img {
            img { width: 100%; }
         }
         h2 { padding: em(10) 0 0; font-size: em(24); font-weight: $semiBold; line-height: em(30); }
         .evaluation { padding: em(7) 0 0; }
         .num_elm { color: #939393; font-size: em(14); font-weight: $semiBold; }
         .star_icon {
            width:em(100); height:em(11); display: inline-block;
            background: url(./../../images/main/star_icon.png) 0 50% repeat;
            background-size: contain;
            &.star_1 { width:em(16); }
            &.star_2 { width:em(32); }
            &.star_3 { width:em(48); }
            &.star_4 { width:em(64); }
            &.star_5 { width:em(80); }
         }
         strong { display: block; padding: em(16) 0 0; font-size: em(28); color: #535353; }
         p {
            padding: em(15) 0 0;
            span { 
               display: block; padding: em(7) 0; font-size: em(18); font-weight: $regular; color: #C0C0C0; border-top: em(1) solid #EAEAEA;
               &:first-of-type { border-top:0; }
            }
         }
      }
      .lineup_bar { 
         width: 77.81%; height: em(10);
         left: 50%; @include translate(-50%,0);
         background-color: #E3E3E3; 
         .swiper-scrollbar-drag { background-color: #C80651; }
      }
   }
   @include mobile() {
      .inner_wrap {
         padding:em(85) 0 em(110) em(40); 
         .swiper { padding: em(70) 0; }
         .swiper-slide {
            h2 { padding: em(20) 0 0; font-size: em(30); line-height: em(40); }
            .num_elm { font-size: em(28); }
            .star_icon {
               width: em(150); height: em(15); background: url(./../../images/main/star_icon_m.png) 0 50% repeat; background-size: contain;
               &.star_1 { width:em(26); }
               &.star_2 { width:em(52); }
               &.star_3 { width:em(68); }
               &.star_4 { width:em(84); }
               &.star_5 { width:em(100); }
            }
            strong {  padding: em(20) 0 0; font-size: em(38); }
            p {
               padding: em(37) 0 0;
              span { padding: em(18) 0; font-size: em(28); border-top: em(2) solid #EAEAEA; }
            }
         }
         .lineup_bar { width: 95%; height: em(6); left: 0; @include translate(0,0); }
      }
   }
}